export default class EventDate {

  constructor(date) {
    this.date = date ? date : new Date();
  }

  static fromSeconds(secs) {
    const date = new Date(secs * 1000);
    return new EventDate(date);
  }

  get year() {
    return this.date.getFullYear();
  }

  get month() {
    return this.date.getMonth() + 1
  }

  toDate() {
    return this.date
  }
}

